button.neutral {
  background: white;
  border-radius: 999em;
  font-size: 15px;
  font-weight: 600;
  padding: 10px 15px;
  border: 0;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);
  outline: none;
  transition: 0.1s transform ease-out;
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%) scale(1);
  display: flex;
  align-items: center;
  white-space: nowrap;
}

button.neutral:hover {
  cursor: pointer;
  background-color: #f5f5f5;
}

button.neutral:active {
  transform: translateX(-50%) scale(0.95);
}
